$hoverBreak: 1200px;
$hoverBreakMobile: 600px;

.project {
  min-height: 100vh;
  width: 80%;
  margin: 0 auto;
  color: white;

  p {
    font-family: 'Ubuntu';
    font-size: 20px;
  }

  @media (max-width: $hoverBreak) {
    font-size: 20px;
  }

  @media (max-width: $hoverBreakMobile) {
    font-size: 18px;
  }

  &__title {
    display: inline-block;
    position: relative;
    padding-top: 90px;
    color: #9da19d;
    font-family: 'Ubuntu';
    font-size: 50px;
    margin-bottom: 20px;

    span {
      display: block;
      position: absolute;
      background-color: #ff3d4f;
      width: 80px;
      height: 7px;
      left: -1%;
      border-radius: 10px;
    }
  }

  &__title-h3 {
    color: white;
    padding-top: 25px;
    text-align: center;
  }

  &__text {
    color: white;
    padding-top: 10px;

    @media (max-width: $hoverBreak) {
      font-size: 20px;
    }

    &--botoom {
      text-align: center;
      font-size: 20px;

      span {
        &:hover a {
          border-bottom: #ff3d4f 1px solid;
        }

        &:hover {
          border-bottom: #ff3d4f 1px solid;
        }
        @media (max-width: $hoverBreak) {
          &:hover a {
            border-bottom: none;
          }

          &:hover {
            border-bottom: none;
          }
        }

        cursor: pointer;
        color: #ff3d4f;

        a {
          text-decoration: none;
          color: #ff3d4f;
        }
      }
    }
  }

  &__wrapper {
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

    &__element {
      margin-bottom: 35px;
      padding: 30px;
      height: 30vh;
      overflow: hidden;
      flex-basis: 26%;
      transition: 1s;
      border-radius: 4px;
      transition: 1s;
      background-color: transparent;
      border: #303133 solid 2px;

      @media (max-width: $hoverBreak) {
        flex-basis: 40%;
        min-height: 30vh;
      }

      @media (max-width: $hoverBreakMobile) {
        flex-basis: 80%;
        min-height: 40vh;
      }

      h3 {
        font-family: 'Ubuntu';

        @media (max-width: $hoverBreak) {
          font-size: 25px;
        }

        @media (max-width: $hoverBreakMobile) {
          font-size: 20px;
        }
      }

      p {
        font-family: 'Ubuntu';
        padding-bottom: 20px;

        @media (max-width: $hoverBreak) {
          font-size: 22px;
        }

        @media (max-width: $hoverBreakMobile) {
          font-size: 15px;
        }
      }

      &__container {
        position: relative;
        width: 100%;
        transition: 1s;
        height: 45%;

        &__link {
          position: absolute;
          left: 50%;
          top: 175%;
          transform: translateX(-50%);
          text-decoration: none;
          color: white;
          background-color: #222222;
          border: #303133 solid 2px;
          padding: 10px 25px;
          border-radius: 4px;
          font-family: 'Ubuntu';
          letter-spacing: 1px;
          font-weight: 700;
          z-index: 999;
          font-size: 18px;

          @media (min-width: $hoverBreak) {
            transition: 1s;
            &:hover {
              background-color: #ff3d4f;
            }
          }

          &:active {
            background-color: #ff3d4f;
          }
        }

        &__img {
          text-align: center;
          i {
            font-size: 100px;
          }
        }
      }
    }
  }
}
